.TaskBar{
    bottom: 0;
    left: 0;
    width: 100vw;
    position: fixed;
    background: rgba(0, 0, 0, 0.158);
    height:calc(5% + .5em);
    display: grid;
    align-content: center;
    padding: 0;
    margin: 0;
}
.Start{
    width:4.5vh;
    height:4.5vh;
    background: rgb(255, 255, 255);
    margin: .5em;
    border-radius: 50%;
}