*{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.Win{
    width:50vw;
    height:50vh;
    position: fixed;
    
    box-shadow: 0 0 5px rgba(0,0,0,0.3);
    transition-duration: .1s;
    transition-timing-function:cubic-bezier(0.785, 0.135, 0.15, 0.86);

}
.head{
    display: flex;
    background: rgba(243, 175, 175, 0.096);
    display: grid;
    
    grid-template: 'm t' 2.5vh /7.5vh calc(100% - 7.5vh);
    z-index: 5;
    
}
.buttons{
    grid-area: m;
}
.buttons button{
   text-align: center;
    border: none;
    width: 2.5vh;
    height: 2.5vh;
    
}
.buttons button:hover{
    transition-duration: .1s;
    background-size: 100%;
    border-radius: 50%;
    transform: scale(1.5);
    
}
.close{
    background: rgba(255, 0, 0, 0.349);
}
.maxi{
    background-color: rgba(0, 255, 13, 0.527);

}
.mizi{
    background-color: rgba(0, 199, 10, 0.507);
}
.mini{
    background-color: rgba(255, 166, 0, 0.315);
}
.mini:hover{
    background-image:url('./img/mini.png') ;
    background-color: rgb(255, 166, 0);
}
.mizi:hover{
    background-image: url('./img/mizi.png');
    background-color: rgb(0, 199, 10);
}
.close:hover{
    
    background-image: url('./img/close.png');
    background-color: red;
}
.maxi:hover{
  
    background-image: url('./img/maxi.png');
    background-color: rgb(0, 255, 13);
}
.title{
    grid-area: t;
    position: absolute;
    left: 50%;
    transform: translate(-50%,0);
    text-align: center;
    align-content: center;
    font-weight: 500;
}
#resize{
            position: absolute;
            display: grid;
            align-content: center;
            text-align: center;
            font-family: 'Courier New', Courier, monospace;
            border-radius: 50%;
            bottom: 0;
            right: 0;
            width: 2.5vh;
            height:2.5vh;
            font-weight: 600;
            background-size: 100%;
            cursor:nw-resize;
            background-image: url('./img/resize.ico');
            background-color: rgba(161, 161, 161, 0.295);
            cursor: none;
            z-index: 5;
}
#resize:hover{
    transform: scale(1.5);
    background-color: #555;
}
.iframe{
    width: 100%;
    height: calc(100% - 2.5vh);
    margin: 0;
    padding: 0;
    border: none;

}